.main-nav__toggle {
  position: relative;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: #283645;
  cursor: pointer;
  border: none;
  outline: none;
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  right: 0;
  width: 50px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
